<template>
  <div class="entMain">
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="14" :offset="5">
          <div class="content-left">
            <div class="title">
              <span>
                <b>在招职位</b>
                ({{entJobInfo.length}})
              </span>
              <router-link to="addjob">
                <el-button style="float:right;" type="success">发布职位</el-button>
              </router-link>
            </div>
            <div v-if="entJobInfo.length==0">
              <p>
                暂无数据，请先
                <router-link to="addjob">发布职位</router-link>
              </p>
            </div>
            <el-table v-else :data="tempList" stripe style="width: 100%" size="small">
              <el-table-column prop="jobName" label="职位" width="180"></el-table-column>
              <el-table-column prop="jobPay" label="薪资"></el-table-column>
              <el-table-column prop="degree" label="学历"></el-table-column>
              <el-table-column prop="workExp" label="经验"></el-table-column>
              <el-table-column prop="updateTime" label="日期" sortable width="180"></el-table-column>
              <el-table-column label="操作" width="100">
                <template slot-scope="scope">
                  <el-button @click="funDelJob(scope.row)" type="text" size="small">删除</el-button>
                  <router-link :to="{name: 'addjob', query:{jobid: scope.row.jobid}}">
                    <el-button type="text" size="small">编辑</el-button>
                  </router-link>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              layout="prev, pager, next"
              :total="entJobInfo.length"
              style="text-align:center;"
              class="page"
              @size-change="funSizeChange"
              @current-change="funCurrentChange"
              :current-page="currentPage"
              :page-size="pageSize"
            ></el-pagination>
            
            <!-- <div v-else v-for="(item,index) in entJobInfo" :key="index"> -->
            <!-- <div v-else v-for="(item,index) in tempList" :key="index"> -->
            <!-- <el-card class="box-card">
                <div>
                  <span>
                    {{item.jobName}}
                    <span class="update-time">({{item.updateTime}})</span>
                  </span>
                  <span class="ent-name">
                    <el-button
                      style="color:#67C23A;float:right;margin-left:5px"
                      type="text"
                      @click="funDelJob(item.jobid,index)"
                    >删除</el-button>
                    <router-link :to="{name: 'addjob', query:{jobid: item.jobid}}">
                      <el-button style="color:#67C23A;float:right;" type="text">编辑</el-button>
                    </router-link>
                  </span>
                  <br>
                  <span class="pay">{{item.jobPay}}</span>|
                  <span>{{item.degree}}</span>|
                  <span>{{item.workExp}}</span>
                </div>
            </el-card>-->
            <!-- <p>
                <span class="job-list">
                  <b>{{item.jobName}}</b>
                  {{item.jobPay}}
                  <span>|</span>
                  {{item.degree}}
                  <span>|</span>
                  {{item.workExp}}
                </span>
                <el-button
                  style="color:#67C23A;float:right;margin-left:5px"
                  type="text"
                  @click="funDelJob(item.jobid,index)"
                >删除</el-button>
                <router-link :to="{name: 'addjob', query:{jobid: item.jobid}}">
                  <el-button style="color:#67C23A;float:right;" type="text">编辑</el-button>
                </router-link>
            </p>-->
            <!-- <p>{{item.updateTime}}发布</p> -->
            <!-- <hr> -->
            <!-- </div> -->

          </div>
        </el-col>
      </el-row>
    </div>

    <!-- <hr> -->
    <!-- <hr> -->
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "entMain",
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      tempList: [],
      entJobInfo: []
    };
  },
  created() {
    this.axios
      .post("getEntBaseInfo", {
        entid: Cookies.get("entid"),
        key: "0000"
      })
      .then(data => {
        if (data.data.status == "ok") {
          let arr = JSON.parse(data.data.data.entJobInfo);
          for (var i in arr) {
            arr[i].fields.jobid = arr[i].pk;
            this.entJobInfo.push(arr[i].fields);
            this.tempList = this.entJobInfo.slice(0, 10);
          }
        }
      });
  },
  methods: {
    funDelJob(item) {
      this.entJobInfo.splice(this.entJobInfo.indexOf(item), 1);
      this.axios
        .post("delEntJobInfo", {
          msg: item.jobid
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
            this.tempList = this.entJobInfo.slice(0, 10);
          }
        });
    },
    funSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.funCurrentChange(this.currentPage);
    },
    funCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.currentChangePage(this.entJobInfo, currentPage);
    },
    //分页方法
    currentChangePage(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize;
      let to = currentPage * this.pageSize;
      this.tempList = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempList.push(list[from]);
        }
      }
    }
  },
  computed: {}
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left .title {
  font-size: 16px;
  color: #606266;
}
.content-left .el-tag {
  font-size: 12px;
  color: #606266;
}

.el-tag + .el-tag {
  margin: 10px;
}
.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}
.content-left p {
  font-size: 16px;
  color: #606266;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
.comment {
  padding: 60px;
  /* background: #67c23a; */
  height: 36px;
  color: #303133;
  font-size: 18px;
  line-height: 36px;
}

.job-list {
  font-size: 14px;
  color: #606266;
}
.title {
  border-bottom: 1px solid #ddd;
  padding-bottom: 20px;
}

.update-time {
  color: #909399;
  font-size: 14px;
}
.pay {
  color: #f56c6c;
}
.ent-name {
  float: right;
}
</style>
